@import "variables";  // '~' informs sass-loader that is not relative path

.mission-links-container {
  display: flex;
  background-image: url(~assets/img/rocket.png);
  background-position: center;
  background-size: cover;
  height: 600px;
  justify-content: center; }

.rounded-border-button {
  color: $white-color;
  background: none;
  border-color: $white-color;
  outline: none;
  max-height: 30px;
  border-radius: 100px;
  border-width: 1px;
  border-style: solid;
  padding-left: 5%;
  padding-right: 5%;
  height: 30px;
  margin-left: 5%;
  &:hover {
    color: $desert-color;
    border-color: $desert-color; } }

.bottom {
  width: 30%;
  align-self: flex-end;
  margin-bottom: 5%; }

.mission-links-title {
  color: $white-color;
  letter-spacing: 15px;
  text-align: center; }
